.cursos{
    background-color: var(--gris);
}

.card-cursos{
    width: 100%;
    background-color: white;
    padding: 1rem;
    border-radius: .5rem;
    margin-bottom: 1rem;
    box-shadow: 2px 5px 20px rgba(53, 60, 63, .5);
}
.card-cursos h4{
    font-family: 'Alata', sans-serif;
    font-weight: 400;
    font-size: 2rem;
    text-align: center;
    color: var(--cafe);
}
.card-cursos p{
    color: var(--cafe);
    font-size: 1.5rem;
    justify-content: flex-start;
    flex-wrap: nowrap;
}
.card-cursos p i{
    width: 3rem;
    color: var(--gris);
}
.card-cursos p span{
    flex-grow: 1;
}

@media only screen and (min-width: 76.8rem){
   .card-cursos{
     width: calc(33.3% - .5rem);
    }
    .card-cursos p{
        font-size: 1.8rem;
    }
}
