.technologies{
    width: 100%;
    min-height: 100vh;
    position: relative;
    overflow: hidden;
}

.tech-card{
    background-color: var(--gris);
    font-family: 'Alata', sans-serif;
    padding: 1rem;
    color: var(--cafe);
    width: calc(50% - .5rem);
    margin-bottom: 1rem;
    font-size: 1.6rem;
    transition: all .6s ease;
    border-radius: .5rem;
}
.tech-icon{
    font-size: 2rem;
}
.mt-6{
    margin-top: 6rem;
}
@media only screen and (min-width: 64rem){
   .tech-card{
     width: 25rem;
     font-size: 2rem;
    }
    .tech-card:hover{
        cursor: pointer;
        transform: scale(1.2);
        background-color: var(--cafe);
        color: var(--gris);
    }
    .tech-icon{
        font-size: 3rem;
    }
}
