.ending{
    width: 100%;
    min-height: 30vh;
    background: url(../../assets/img/ending.jpg);
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
}
.ending::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, .9);
}
.ending .container{
    position: relative;
    z-index: 1;
    padding: 3rem 0;
}

.dif-card{
    width: 50%;
    text-align: center;
    color: var(--gris);
    margin-bottom: 6rem;
}
@media only screen and (min-width: 76.8rem){
  .dif-card{
      width: 33.3%;
  }
}
.dif-card i{
    font-size: 4rem;
    margin-bottom: 1.5rem;

    background-image: linear-gradient(to bottom right, var(--naranja), #a80077);
    -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.dif-card h2{
    font-family: 'Alata', sans-serif;
    font-weight: 400;
    text-transform: uppercase;
}
