.cover{
    background: url(../../assets/img/cover-bgd1.jpg);
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    text-align: center;
    color: white;
}
.cover::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-image: linear-gradient(to bottom right, rgba(117, 104, 103, .9), rgba(53, 60, 63, .9), rgba(53, 60, 63, .9));
}
.cover .container{
    position: relative;
    z-index: 1;
}
.cover-profile-pic{
    width: 15rem;
    height: 15rem;
    border-radius: 50%;
    box-shadow: .2rem .2rem .5rem var(--color1);
}
.cover-titulo{
    font-size: 4rem;
    font-family: 'Alata', sans-serif;
    font-weight: 400;
}
@media only screen and (min-width: 76.8rem){
  .cover-titulo{
      font-size: 7rem;
  }
}
.cover-profession{
    text-transform: uppercase;
    letter-spacing: 1rem;
    font-weight: 400;
}

.down-arrow{
    font-size: 4rem;
    font-weight: 100;
    position: absolute;
    width: 100%;
    bottom: 0;
    padding: 3rem 0;
}
.down-arrow i{
    border: 1px solid transparent;
    border-radius: 50%;
    width: 6rem;
    height: 6rem;
    padding-top: 1rem;
    transition: all .3s ease;
}
.down-arrow i:hover{
    cursor: pointer;
    border: 1px solid white;
    border-radius: 50%;
}
