html{
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
body{
  font-size: 1.6rem;
  margin: 0;
  font-family: 'Alata', sans-serif;
  font-family: 'Flamenco', cursive;
  color: var(--navy);
}
*, *:before, *:after{
  box-sizing: inherit;
}
img{
  width: 100%;
  max-height: 100%;
}
a{
  text-decoration: none;
}
a:hover{
  cursor: pointer;
}
input:focus,button:focus{
  outline: none;
}
li{
  list-style: none;
}
section{
  min-height: 100vh;
  padding: 9rem 0;
}
h1,h2,h3{
  margin-top: 0;
}
p{
  font-size: 2rem;
  line-height: 1.5;
  margin: 0;
  color: var(--cafe);
}


/* width */
::-webkit-scrollbar {
  width: .5rem;
  height: .3rem;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
  background-color: var(--gris);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--cafe);
  border-radius: .5rem;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--naranja);
}

/*
General
*/
:root{
  --navy: rgb(53, 60, 63);
  --gris: rgb(213, 214, 210);
  --cafe: rgb(117, 104, 103);
  --naranja:  rgb(255, 141, 63);
}
.container{
  max-width: 120rem;
  margin: 0 auto;
  padding: 0 1rem;
}
.titulo{
  font-size: 3.8rem;
  font-family: 'Alata', sans-serif;
  font-weight: 400;
  position: relative;
}
.titulo::before{
  content: '|';
  margin-right: 2rem;
  color: var(--naranja);
  color: var(--cafe);
}
.subtitulo{
  font-size: 3rem;
  font-family: 'Flamenco', cursive;
  font-weight: 400;
  text-transform: uppercase;
}


.flex{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.emphasis{
  font-size: 2.3rem;
  font-family: 'Alata', sans-serif;
  font-weight: 300;
}

@media only screen and (min-width: 76.8rem){
  .container{
    padding: 0 3rem;
  }
  .titulo{
    font-size: 7rem;
  }
  .subtitulo{
    font-size: 5rem;
  }
  p{
    font-size: 2.5rem;
  }
  .emphasis{
    font-size: 3rem;
  }
}

.naranja{
  color: var(--naranja);
}
.light{
  color: var(--gris);
}
.cafe{
  color: var(--cafe);
}
.gris{
  color: var(--gris);
}
.navy{
  color: var(--navy);
}
