.footer{
    width: 100%;
    min-height: 30vh;
    background-color: var(--navy);
    padding: 3rem 0;
}
.footer-container{
    width: 100%;
    padding: 1rem;
}
@media only screen and (min-width: 76.8rem){
  .footer-container{
      width: 50%;
      padding: 3rem;
  }
}
.footer-container h2{
    text-align: center;
}

.div-form{
    margin-bottom: 3rem;
}
.div-form label{
    color: var(--gris);
    font-size: 2rem;
    padding-left: .5rem;
}
.div-form input{
    width: 100%;
    font-size: 1.6rem;
    padding: 1rem;
    color: var(--cafe);
    border-radius: .5rem;
    border: none;
    background-color: var(--gris);
    color: var(--navy);
}

.div-form textarea{
    width: 100%;
    height: 10rem;
    border-radius: .5rem;
    background-color: var(--gris);
    border: none;
    outline: none;
    resize: none;
    padding: 1rem;
}
.button{
    font-size: 2rem;
    background-color: var(--cafe);
    color: var(--gris);
    border: none;
    padding: 1rem;
    border-radius: .5rem;
    width: 10rem;
    margin: 0 auto;
    transition: all .3s ease;
}
.button:hover{
    cursor: pointer;
    background-color: var(--naranja);
}
.alert{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(37, 44, 47, 0.5);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    z-index: 100;
    padding-top: calc(50vh - 10rem);
}
.message{
    width: 40rem;
    height: 20rem;
    border-radius: .5rem;
    margin: 0 auto;
    background-color: var(--gris);
    padding: 1rem;
    text-align: center;
    font-family: 'Alata', sans-serif;
}
.message p{
    color: rgb(0, 138, 0);
    margin-bottom: 3rem;
}
.message h2{
    font-weight: 400;
}
